er {\n  display: grid;\n  grid-template-columns: repeat(7, 1fr);\n  gap: 1rem;\n  margin-top: 1.2rem;\n}\n\n.forecast-daily-day {\n  font-size: 0.95rem;\n}\n\n.forecast-daily-temp-max {\n  font-size: 1.8rem;\n  margin: 0.7rem 0 0 0;\n}\n\n.forecast-daily-temp-min {\n  font-size: 0.85rem;\n  margin-left: 5px;\n  margin-bottom: 0.5rem;\n}\n\n.weather-daily-icon {\n  width: 35px;\n}\n\n.forecast-selected {\n  border: 2px solid #000;\n  border-radius: 4px;\n}\n\n.change-hours {\n  display: none;\n  position: absolute;\n  left: 9rem;\n  align-items: center;\n  gap: 0.2rem;\n  margin-bottom: 0.3rem;\n}\n\n.dot {\n  border: 2px solid #000;\n  width: 10px;\n  height: 10px;\n  border-radius: 50%;\n  margin-top: 0.3rem;\n  cursor: pointer;\n}\n\n.dot-selected {\n  background: #000;\n}\n\n.change-hours__left,\n.change-hours__right {\n  font-size: 2.3rem;\n  cursor: pointer;\n  margin-bottom: 0.4rem;\n}\n\n.forecast-hourly-main-container {\n  display: none;\n  margin-top: 1.2rem;\n}\n\n.forecast-hourly-container {\n  display: grid;\n  grid-template-columns: repeat(8, 1fr);\n  gap: 1rem;\n  margin-top: 1.2rem;\n}\n\n.forecast-hourly {\n  display: grid;\n  grid-template-columns: 1fr;\n}\n\n.forecast-hourly-time {\n  font-size: 0.95rem;\n}\n\n.forecast-hourly-temp {\n  font-size: 1.8rem;\n  margin-top: 0.7rem;\n  margin-bottom: 0.5rem;\n}\n\n.forecast-hourly-icon {\n  width: 35px;\n}\n\n.error-msg {\n  margin-top: 0.5rem;\n  font-size: 0.9rem;\n  visibility: hidden;\n}\n\n@media only screen and (max-width: 560px) {\n  .daily-btn,\n  .hourly-btn {\n    font-size: 0.7rem;\n    padding: 0.25rem 0.5rem;\n  }\n\n  .forecast-daily-container {\n    gap: 0.5rem;\n    margin-top: 0.6rem;\n  }\n\n  .forecast-daily-day {\n    font-size: 0.85rem;\n  }\n\n  .forecast-daily-temp-max {\n    font-size: 1.35rem;\n    margin: 0;\n  }\n\n  .forecast-daily-temp-min {\n    font-size: 0.8rem;\n    margin-left: 0;\n    margin-bottom: 0.4rem;\n  }\n\n  .weather-daily-icon {\n    width: 25px;\n  }\n\n  .forecast-selected {\n    border: 2px solid #000;\n    border-radius: 4px;\n  }\n\n  .change-hours {\n    left: 7rem;\n    gap: 0.15rem;\n    margin-bottom: 0.7rem;\n  }\n\n  .dot {\n    border: 1.5px solid #000;\n    width: 8px;\n    height: 8px;\n    margin-top: 0.7rem;\n  }\n\n  .change-hours__left,\n  .change-hours__right {\n    font-size: 2.2rem;\n  }\n\n  .forecast-hourly-main-container {\n    margin-top: 0.6rem;\n  }\n\n  .forecast-hourly-time {\n    font-size: 0.85rem;\n    text-align: center;\n  }\n\n  .forecast-hourly-temp {\n    font-size: 1.35rem;\n    margin: 0.4rem 0 0.2rem 0;\n  }\n\n  .forecast-hourly-icon {\n    width: 25px;\n  }\n}\n\n@media only screen and (max-width: 460px) {\n  .weather-info__description {\n    font-size: 1.5rem;\n  }\n\n  .weather-info__icon {\n    width: 65px;\n    height: 50px;\n  }\n\n  .weather-day-date,\n  .weather-time {\n    font-size: 0.9rem;\n  }\n\n  .weather-info__temperature {\n    font-size: 2rem;\n  }\n\n  .weather-info__city {\n    font-size: 1.4rem;\n  }\n\n  section {\n    margin: 1.5rem 0 2rem 0;\n  }\n\n  .weather-details img {\n    width: 30px;\n  }\n\n  .weather-detail-data {\n    margin-left: 0.5rem;\n  }\n\n  .weather-detail-data h5 {\n    font-size: 0.7rem;\n  }\n  .weather-detail-data h4 {\n    font-size: 1.2rem;\n  }\n\n  .forecast-daily-container {\n    display: grid;\n    grid-template-columns: 1fr;\n    gap: 0.7rem;\n    margin-top: 1rem;\n  }\n\n  .forecast-daily {\n    display: grid;\n    grid-template-columns: 1fr 1fr 1fr;\n    align-items: center;\n    justify-items: center;\n  }\n\n  .forecast-daily-day {\n    font-size: 1rem;\n  }\n\n  .forecast-daily-temp {\n    display: flex;\n    align-items: center;\n  }\n\n  .forecast-daily-temp-max {\n    font-size: 1.4rem;\n    margin: 0;\n  }\n\n  .forecast-daily-temp-min {\n    font-size: 0.85rem;\n    margin-left: 10px;\n    margin-top: 4px;\n  }\n\n  .weather-daily-icon {\n    width: 30px;\n  }\n\n  .forecast-hourly-main-container {\n    margin-top: 1rem;\n  }\n\n  .forecast-hourly-container {\n    display: grid;\n    grid-template-columns: 1fr;\n    align-items: center;\n    gap: 0.7rem;\n  }\n\n  .forecast-hourly {\n    display: grid;\n    grid-template-columns: repeat(3, 1fr);\n    align-items: center;\n    justify-items: center;\n  }\n\n  .forecast-hourly-time {\n    font-size: 1rem;\n  }\n\n  .forecast-hourly-temp {\n    font-size: 1.3rem;\n    margin: 0;\n  }\n\n  .forecast-hourly-icon {\n    width: 30px;\n  }\n\n  .error-msg {\n    margin-top: 0.2rem;\n    font-size: 0.75rem;\n    font-weight: 600;\n  }\n}\n\n#hourly-time-9,\n#hourly-time-10,\n#hourly-time-11,\n#hourly-time-12,\n#hourly-time-13,\n#hourly-time-14,\n#hourly-time-15,\n#hourly-time-16,\n#hourly-time-17,\n#hourly-time-18,\n#hourly-time-19,\n#hourly-time-20,\n#hourly-time-21,\n#hourly-time-22,\n#hourly-time-23,\n#hourly-time-24 {\n  display: none;\n}\n"],"sourceRoot":""}]);
